.wp-block-pm-perecs {

	display: flex;
	flex-direction: row;
	gap: var(--wp--preset--spacing--50);

	width: calc(100% + 10rem);
	max-width: none;
	margin-left: -5rem !important;

  @media (max-width: 60rem) {
	width: 95%;
	margin-left: auto !important;
  }

  .pm-content__mobile,
  .pm-content__mobile.visible {
		display: none;
	}

    @media (max-width: 60rem) {

		.perec-column__content {
			display: none;
		}

		.pm-content__mobile.visible {
			display: block;
		}
	}

	@media (max-width: 60rem) {
		flex-direction: column;
	}

	.perec-column__index {
		width: 10rem;

	@media (max-width: 60rem) {
		width: auto;
	}
		flex-shrink: 0;
		.perec-index__item.pm-selected {
			background-color: var(--wp--preset--color--primary-light);
			font-size: 1.1em;
			.perec-index__author{
				color: var(--wp--preset--color--primary);
			}
		}
		.perec-index__item {
			padding: var(--wp--preset--spacing--30) var(--wp--preset--spacing--20);
			.perec-index__author.has-100-font-size {
				text-align: left!important;
			}
			.perec-index__title {
				margin: var(--wp--preset--spacing--10) 0;
				font-style: italic;
				&:hover {
					cursor: pointer;
					color: var(--wp--preset--color--primary)
				}			
			}
			.perec-index__title.pm-selected {
				color: var(--wp--preset--color--primary);
			}
		}
		.perec-index__item:not(:last-child) {
			border-bottom: 1px solid var(--wp--preset--color--base-darker);
		}
	}
	.perec-column__content {
		padding: var(--wp--preset--spacing--60) var(--wp--preset--spacing--60);
		flex-grow: 1;
		height: fit-content;	
		background-color: var(--wp--preset--color--primary-light);
		font-size: .8rem;
		line-height: 1.6;
		.perec-content__text p {
			font-style: italic;
		}
		.perec-content__signature {}
	}

	.pm-icon__plume {
		background-image: url(../../../assets/images/plume--mini.webp);
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		aspect-ratio: 817/1209;
		width: 2.5rem;
		margin: auto;
		margin-bottom: 1.5rem;
	}

}